.formPage {
    background-size: cover;
    background-position: center;
    background-position: center;
    max-width: 1440px;
    height: 100vh;
    padding-top: 100px;
    padding-bottom: 100px;
    align-items: center;
    justify-content: space-evenly;
  }
  
  .title {
    font-size: 60px;
    color: rgb(28, 15, 2);
    font-weight: 700;
    margin-right: 10vw;
    margin-left: 10vw;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    align-items: center;

  }
  .container {
    display:block;
    justify-content: center;
  }
  
  .label {
    font-size: 25px;
    line-height: 1.36;
    letter-spacing: 0.1em;
    font-weight: 700;
    margin-bottom: 40px;
    width: 320px;
    color: rgb(28, 15, 2);
  }
  
  .label:focus-within {
    color: rgb(7, 180, 65);
  }
  
  .input {
    width: 280px;
    height: 24px;
    border-top: none;
    border-left: none;
    border-right: none;
    padding-left: 30px;
    background-color: transparent;
  
    font-size: 30px;
    border-bottom: 1px solid rgb(28, 15, 2);
    color: rgb(28, 15, 2);
  }
  .input:focus {
    outline: none;
  }
  
  input:focus-within {
    border-bottom: 1px solid rgb(7, 180, 65);
  }
  
  .button {
    width: 220px;
    height: 80px;
    font-size: 40px;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    padding: 0;
    margin-top: 20px;
  
    box-shadow: 2px 10px 10px rgba(0, 0, 0, 0.85);
  }
  
  .button:hover,
  .button:focus {
    cursor: pointer;
    color: rgb(7, 180, 65);
  }
  
  .massage {
    color: rgb(7, 180, 65);
    font-size: 40px;
    line-height: 1.17;
    letter-spacing: 0.05em;
    min-height: 200px;
    max-width: 300px;
  }
  